}

/* Accordian */
.faqs{
  
 
  padding: 15px;
}
.faqs .faq{
  margin: 15px;
  padding: 15px;
  background-color: #fff;
  border-radius: 8px;
  box-shadow: 0px 0px 10px rgba(0,0,0,0.2);

}

.faqs .faq .faq-question{
  position: relative;
  font-size: 20px;
  padding-right: 80px;
  transition: all 0.4s ease;
  
}
.faqs .faq .faq-question::after{
  content: '';
  position: absolute;
  top:50%;
  right: 0px;
  width: 20px;
  height:20px;
  transform: translateY(-50%) ;
  background-image: url('https://image.flaticon.com/icons/svg/892/892630.svg');
  background-repeat: no-repeat;
  transition: all 0.4s ease;

}
.faqs .faq .faq-answers{
  opacity: 0;
  max-height: 0;
  overflow-y: hidden;
  transition: all 0.4s ease;
}

.faqs .faq.open .faq-question{
  margin-bottom: 15px;

}

.faqs .faq.open .faq-question::after{
  transform: translateY(-50%) rotate(180deg);
}

.faqs .faq.open .faq-answers{
  max-height: 1000px;
  opacity: 1;
}


.social-links{
  background-color: black;
  opacity: .8;
  width:75%;
  color: white;
  margin: auto;
  border-radius: 10px;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
}
.social-links img{
  height: auto;
  width: 100px;
  margin: 5px;
}
.social-links::before{
  content: attr(data-tooltip);
}


/* The ToolTip Portion */

.html{
  position: relative;
}
.html::before,
.html::after{
 --scale:0;
 --arrow-size:10px;
 --translate-y:0%;
  position: absolute;
  top:-.25rem;
  
  left: 50%;
  
  
  transform: translateX(-50%) translateY(-100%) scale(var(--scale));
  transition: 100ms transform;
  transform-origin: bottom-center;
}

.html::before{
  --translate-y:calc(-100%-var(--arrow-size));
  content: attr(data-tooltip);
  height: max-content;
  padding: .5rem;
  max-width: 100%;
  width:max-content;
  background-color: black;
  border-radius: .3rem;
  text-align: center;
}
.html:hover::before,
.html:hover::after{
--scale:1;
}



